.news-item {
  margin: 0.1rem;
  display: block;
  color: #000;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  padding: .25rem 0;
  border-bottom: 0.01rem solid #ccc;
}
.left {
  text-align: left;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin-right: .1rem;
}
.right {
  -ms-flex-negative: 0;
      flex-shrink: 0;
  width: 2.3rem;
  height: 1.5rem;
  background-size: cover;
  background-position: center;
}
.title {
  font-size: .3rem;
}
.span {
  font-size: .24rem;
}


